/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: rgba(0,0,0,0);
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius:0.4rem
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}